import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faTrashAlt, faPhoneVolume } from '@fortawesome/free-solid-svg-icons'

import './ListaContatos.css'

export default function ListaContatos({contato, remover}) {
	return(

		<div className="mx-2">
			<div className="container lista-contatos my-2">
				<div className="row text-center">
					<div className="col-6 p-2 text-start">
						<h4>
							<FontAwesomeIcon icon={faUser} className="me-3"/>
							{contato.nome}
						</h4>
					</div>
					<div className="col-5 p-2 text-start">
						<h4>
							<FontAwesomeIcon icon={faPhoneVolume} className="me-3"/>
							{contato.telefone}
						</h4>
					</div>
					<div className="col-1 p-2 text-end">
						<h4>
							<FontAwesomeIcon
								icon={faTrashAlt}
								onClick={()=> remover(contato.id)} />
						</h4>
					</div>
				</div>
			</div>
		</div>

	)
}