import React, { useState, useEffect, useRef, useId } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faList, faTrash, faUserPlus } from '@fortawesome/free-solid-svg-icons'
import './App.css'
import ListaContatos from "./Components/ListaContatos"
import { v4 as uuid } from 'uuid'

export default function App() {

	// States

	const [contato, setContato] = useState({ id: '', nome: '', telefone: '' })

	const [listaContatos, setListaContatos] = useState([])

	const inputNomeRef = useRef()
	const inputTelefoneRef = useRef()
	const idNome = useId()
	const idTelefone = useId()

	// Métodos:

	function cadastrar() {

		if (!contato.nome || !contato.telefone) { return }

		let duplicado = listaContatos.find((c) => c.nome === contato.nome && c.telefone === contato.telefone)

		if (typeof duplicado !== 'undefined') {
			inputTelefoneRef.current.focus()
			alert('Contato já cadastrado')
			return
		}

		setListaContatos((old) => [...old, { ...contato, id: uuid() }])
		inputNomeRef.current.focus()
		setContato({ nome: '', telefone: '' })

	}

	function enterAdicionar(event) {
		if (event.key === "Enter") {
			cadastrar()
		}
	}

	// Persistência no localStorage

	// Pegar os dados do localStrorage no início
	useEffect(() => {
		setListaContatos(JSON.parse(localStorage.getItem("meus_contatos")))
	}, [])

	// Escutar as alterações na lista e salvar no localStorage
	useEffect(() => {
		localStorage.setItem("meus_contatos", JSON.stringify(listaContatos))
	}, [listaContatos])

	// Limpar a lista de contatos

	function limparLocalStorage() {
		setListaContatos([])
	}

	// Remover contato por ID
	function removerContato(id) {
		let tmp = listaContatos.filter(ct => ct.id !== id)

		setListaContatos(tmp)
	}

	return (
		<>

			<div className="container-fluid titulo">
				<div className="row">
					<div className="col text-center">
						<h2 className="text-center"><FontAwesomeIcon icon={faList} className="me-3"/>LISTA DE CONTATOS</h2>
					</div>
				</div>
			</div>


			<div className="container-fluid formulario mb-4">
				<div className="row">
					<div className="col p-3">
						<div className="row justify-content-center">
							<div className="col-10 col-sm-8 col-md-6 col-lg-4">
								<div className="mb-3">
									<label className="form-label" htmlFor={idNome}>Nome</label><br />
									<input ref={inputNomeRef} type="text" value={contato.nome} placeholder="Digite o nome" onChange={e => setContato({ ...contato, nome: e.target.value })} className="form-control" />
								</div>
								<div>
									<label className="form-label" htmlFor={idTelefone}>Telefone</label><br />
									<input ref={inputTelefoneRef} type="number" value={contato.telefone} placeholder="Digite o telefone" onChange={e => setContato({ ...contato, telefone: e.target.value })} onKeyUp={enterAdicionar} className="form-control" />
								</div>

								<div className="row mt-3">
									<div className="col text-start">
										<button onClick={limparLocalStorage} className="btn btn-outline-warning">
											<FontAwesomeIcon icon={faTrash} className="me-3"/>
											Limpar Lista
										</button>
									</div>
									<div className="col text-end">
										<button onClick={cadastrar} id="btnCadastrar" className="btn btn-outline-info">
											<FontAwesomeIcon icon={faUserPlus} className="me-3"/>
											Cadastrar
										</button>
									</div>
								</div>

							</div>
						</div>

					</div>
				</div>
			</div>

			{listaContatos.map(ct => {
				return (
					<ListaContatos key={ct.id} contato={ct} remover={removerContato} />
				)
			})}
		</>
	)
}